@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
html,body{margin:0;font-family:"Raleway", sans-serif}.btn-primary{background-color:#303132;color:#ffffff;padding:10px 60px;transition:all 0.35s ease;font-size:18px;text-transform:capitalize;cursor:pointer;border:thin solid #303132}.btn-primary:hover{background-color:#ffffff;color:#303132;box-shadow:1px 3px 7px 1px rgba(255,255,255,0.57)}.btn-primary:focus{outline:none}.link{text-decoration:none}.lazy{opacity:0}.loaded{opacity:1;transition:opacity 0.45s ease}

.loader{display:inline-block;position:relative;width:100px;height:100px;margin:auto;display:flex;margin-top:calc(50vh - 80px)}.loader div{box-sizing:border-box;display:block;position:absolute;width:64px;height:64px;margin:8px;border:8px solid;border-radius:50%;-webkit-animation:loader 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;animation:loader 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;border-color:#C3A370 transparent transparent transparent}.loader div:nth-child(1){-webkit-animation-delay:-0.45s;animation-delay:-0.45s}.loader div:nth-child(2){-webkit-animation-delay:-0.3s;animation-delay:-0.3s}.loader div:nth-child(3){-webkit-animation-delay:-0.15s;animation-delay:-0.15s}@-webkit-keyframes loader{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes loader{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

